.main {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 400px;
}

@media (min-width: 200px) and (max-width: 550px) {
  form {
    width: 100%;
    flex-direction: column;
    font-size: 1.2rem;
    align-items: center;
  }

  input {
    min-width: 200px;
    width: 100%;
    margin-bottom: 24px;
    padding: 5px;
  }
}
